.auth-container {
  background-color: #f7f7f7;
  border: 1px solid #dc470b;
  padding-top: 20px;
  padding-bottom: 20px;
}

.auth-section {
  padding-top: 20px;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .auth-section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.auth-title {
  justify-content: center;
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: 35px;
  display: flex;
}

.auth-form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.auth-input {
  width: 30vw;
  height: 40px;
  border: 1px solid #545454;
  border-radius: 5px;
  margin-top: 15px;
  padding-left: 5px;
}

.auth-input:hover, .auth-input:focus {
  cursor: pointer;
  border-color: #dc470b;
}

.btn-list {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.btn-item {
  margin-top: 15px;
}

.btn {
  text-align: center;
  width: 130px;
  height: 44px;
  color: #000;
  transition: background-color border-color 250ms cubic-bezier(.4, 0, .2, 1);
  background: none;
  border: 1px solid #000;
  border-radius: 5px;
  margin-right: 20px;
  padding: 14px 36px 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: block;
}

@media screen and (min-width: 768px) {
  .btn {
    width: 152px;
    height: 44px;
    padding: 13px, 46px;
    margin-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .btn {
    width: 148px;
    height: 42px;
    margin-right: 16px;
    padding: 12px 41px;
  }
}

.login-btn {
  background-color: #ff6b01;
  border-color: #ff6b01;
}

.login-btn:hover, .login-btn:focus, .signup-btn {
  background-color: #fff;
  border-color: #545454;
}

.signup-btn:hover, .signup-btn:focus {
  background-color: #ff6b01;
  border-color: #ff6b01;
}

